import React, { useState, useEffect } from 'react'
import '../scss/tableStyles.scss'
import { Carousel } from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
import Axios from 'axios'
import { GET_GAME_TIME, GET_TICKETS_LIST, CUSTOMER_TICKETS } from '../api/Endpoints'
import _ from 'lodash'
import AlarmImage from '../assets/svg/alarm-clock.svg'
import CalendarImage from '../assets/svg/calendar.svg'

const UpcomingGame = () => {
    const [timeData, setTimeData] = useState({})
    const [remainingTime, setRemainingTime] = useState(0)
    const [tickets, setTickets] = useState({})
    const [customerTickets, setCustomerTickets] = useState({})
    const getTimings = async () => {
        try {
            const timings = await Axios.get(GET_GAME_TIME)
            return timings.data
        } catch (e) {
            console.log(e.message)
        }
    }

    const getTickets = async () => {
        try {
            const result = await Axios.get(GET_TICKETS_LIST)
            setTickets(result.data)
        } catch (e) {
            console.log(e.message)
        }
    }

    const handleGetCustomerTickets = async () => {
        try {
            const result = await Axios.get(CUSTOMER_TICKETS)
            setCustomerTickets(result.data.ticket_customers)
        } catch (e) {
            console.log(e.message)
        }
    }

    const findCustomerName = (ticket) => {
        const customer = customerTickets[ticket]
        return undefined == customer ? '' : '(' + customer + ')'
    }

    useEffect(() => {
        const fetchMyAPI = async () => {
            const result = await getTimings()
            setTimeData(result)
            getTickets()
            handleGetCustomerTickets()
            if (!_.isUndefined(result.remaining_time)) {
                let timeMore = Number(result.remaining_time)
                let interval = setInterval(() => {
                    if (timeMore <= 0) {
						clearInterval(interval)
						// UpcomingGame()
					}
                    timeMore--
                    const readableTime = [
                        Math.floor(timeMore / 3600).toString().padStart(2, '0'),
                        Math.floor(timeMore % 3600 / 60).toString().padStart(2, '0'),
                        Math.floor(timeMore % 3600 % 60).toString().padStart(2, '0')
                    ].join(':')
                    setRemainingTime(readableTime)
                }, 1000)
            }
        }
        fetchMyAPI()
    }, [])

    function json2array(json) {
        var result = [];
        var keys = Object.keys(json);
        keys.forEach(function (key) {
            result.push(json[key]);
        });
        return result;
    }
    return (
        <>
            <div className="container mt-5" style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                <Carousel>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2>Joining <span>Tournament</span></h2>
                            <h2 className="mt-2">Choose your coupon</h2>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2><span>Tambola</span></h2>
                            <h2 className="mt-2">Play with your <span>Near</span> &amp; <span>Dear</span></h2>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2>Joining <span>Tournament</span></h2>
                            <h2 className="mt-2">Buy Tickets &amp; <span>Play</span></h2>
                        </div>
                    </Carousel.Item>
                </Carousel>
            </div>
            { !_.isUndefined(timeData.remaining_time) &&
                <table className="container table mt-2 ticket" style={{ backgroundColor: '#1A162F', opacity: '0.9', color: 'white' }}>
                    <tr className="sh">
                        <th colspan="3" style={{ textAlign: 'center' }}>
                            <span style={{ float: 'left' }}>
                                <img src={CalendarImage} className={'mr-2'} width="25px" />
                                {timeData.game_date}
                            </span>
                            {remainingTime}
                            <span style={{ float: 'right' }}>
                                <img src={AlarmImage} className={'mr-2'} height="25px" />
                                {timeData.game_time}
                            </span>
                        </th>
                    </tr>
                </table>}

            { !_.isUndefined(tickets) &&
                <table className="container table">
                    {
                        json2array(tickets).map((obj, idx) => {
                            return (
                                <>
                                    <tr style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                                        <th className="text-center text-light" scope="col" colspan="9">LOTTO TICKET : {obj.no} {findCustomerName(obj.no)}</th>
                                    </tr>
                                    {
                                        json2array(obj.detail).map((numbers, index) => {
                                            return (
                                                <tr className="border-color" key={index}>
                                                    {
                                                        json2array(numbers).map((indexes, id) => {
                                                            return (
                                                                <>
                                                                    <td className={(parseInt(index) + parseInt(id)) % 2 == 0 ? 'bg-info' : 'bg-warning'}>
                                                                        {indexes.number}
                                                                    </td>
                                                                </>
                                                            )
                                                        })
                                                    }
                                                </tr>
                                            )
                                        })
                                    }
                                </>
                            )
                        })
                    }
                </table>}
        </>
    )
}

export default UpcomingGame