import React from 'react'
import { HashRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import Terms from '../pages/Terms.jsx'
import Header from '../components/Header'
import Footer from '../components/Footer'
import UpcomingGame from '../pages/UpcomingGame.jsx'
import TambolaRules from '../pages/TambolaRules.jsx'
import Faq from '../pages/Faq.jsx'

export default () => {
    return (
        <>
            <Router>
                <Switch>
                    <Route exact path="/" component={Terms} />
                    <Route exact path="/upcoming-game" component={UpcomingGame} />
                    <Route exact path="/rules" component={TambolaRules} />
                    <Route exact path="/faq" component={Faq} />
                </Switch>
                <Footer />
            </Router>
        </>
    )
}